@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.6vw;
  }
}
@media (min-width: 1000px) {
  html {
    font-size: 16px;
  }
}

/* pcの電話番号発信対応 */
@media screen and (min-width: 768px) {
  a[href^="tel:"] {
    pointer-events: none;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  transition: opacity 0.3s;
}
@media screen and (min-width: 768px) {
  a:hover {
    opacity: 0.8;
  }
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
  height: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.c-btn {
  color: #fff;
  background-color: #FF8043;
  font-size: 1.25rem;
  text-align: center;
  letter-spacing: 0.1875rem;
  border-radius: 0.5rem;
  padding: 1rem 4rem;
  display: inline-block;
}

.p-btn {
  width: 100%;
  text-align: center;
  margin-top: 3.125rem;
}
@media screen and (min-width: 768px) {
  .p-btn {
    margin-top: 3.75rem;
  }
}

.p-btn--work {
  margin-top: 7.5rem;
}

/* おすすめ物件Part2 */
.c-info-cards {
  width: 100%;
  display: flex;
  gap: 1.25rem;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .c-info-cards {
    gap: 1.875rem;
    flex-direction: row;
    align-items: initial;
    flex-wrap: wrap;
  }
}

.c-info-card__2 {
  display: block;
  width: 60%;
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .c-info-card__2 {
    width: calc((100% - 3.75rem) / 3);
  }
}

.c-info-card__img2 {
  position: relative;
}

.c-info-card__img2::before {
  content: "";
  display: block;
  padding-top: 69.1176470588%;
}

.c-info-card__img2 img {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  object-fit: cover;
}

.c-info-card__bottom {
  background-color: rgba(11, 63, 31, 0.76);
  margin: -1.8125rem 0 0;
  padding: 0.625rem 1.25rem;
  position: relative;
}

.c-info-card__coment {
  font-size: 0.75rem;
  font-weight: 200;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.c-info-card__name {
  font-size: 1.125rem;
  font-weight: 500;
  color: #fff;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.c-info-card__price {
  display: flex;
  align-items: baseline;
  white-space: nowrap;
}

.c-info-card__ttl {
  font-size: 0.875rem;
  color: #fff;
}

.c-info-card__int {
  text-overflow: ellipsis;
  overflow: hidden;
  color: #fff;
}

.c-info-card__int span {
  font-size: 1.9375rem;
  font-weight: 700;
  color: #FF8043;
  line-height: 1.3;
  letter-spacing: 0.3125rem;
  vertical-align: -0.125rem;
  margin: 0 0 0 0.3125rem;
}

.c-info-card__space {
  display: flex;
  align-items: baseline;
  white-space: nowrap;
}

.c-info-card__ttl-bottom {
  font-size: 0.875rem;
  color: #fff;
}

.c-info-card__tex {
  text-overflow: ellipsis;
  overflow: hidden;
  color: #fff;
  display: block;
}

.c-msg {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media screen and (min-width: 768px) {
  .c-msg {
    flex-direction: row;
