  font-size: 1rem;
  text-align: center;
  padding: 4.375rem 0.9375rem;
  border-radius: 27% 73% 40% 60%/62% 48% 52% 38%;
  background-color: #66CDAA;
}

.c-msg__img img {
  width: 18.75rem;
  border-radius: 30% 70% 70% 30%/30% 34% 66% 70%;
  margin-top: -3.125rem;
}
@media screen and (min-width: 768px) {
  .c-msg__img img {
    width: 14.375rem;
    margin-top: initial;
    position: relative;
    left: -0.9375rem;
  }
