.c-section-titles {
  font-family: "Murecho", sans-serif;
}

.c-section-title__top {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.375rem;
  text-align: center;
  color: #143423;
}
@media screen and (min-width: 768px) {
  .c-section-title__top {
    font-size: 1.625rem;
  }
}

.c-section-title__bottom {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.125rem;
  text-align: center;
  color: #143423;
}
@media screen and (min-width: 768px) {
  .c-section-title__bottom {
    font-size: 1.25rem;
  }
}

.c-section-title__bottom::before,
.c-section-title__bottom::after {
  content: "";
  background-color: #143423;
  height: 0.1875rem;
  width: 3.125rem;
}
@media screen and (min-width: 768px) {
  .c-section-title__bottom::before,
.c-section-title__bottom::after {
    width: 4.375rem;
  }
}

.c-section-title__bottom::before {
  margin-right: 1.25rem;
}

.c-section-title__bottom::after {
  margin-left: 1.25rem;
}

