  position: relative;
}

.p-work__text {
  text-align: center;
  font-size: 1.375rem;
  color: #143423;
  padding-top: 1.875rem;
}

.p-work__top {
  display: inline-flex;
}

.p-work__bottom {
  display: flex;
}

.p-work__sp {
  margin-top: 3.125rem;
}
@media screen and (min-width: 768px) {
