.p-work__btn-center {
  position: relative;
  z-index: 1;
  width: 100%;
  display: block;
  margin: 0 auto;
}

.p-work__brock {
  display: grid;
  position: absolute;
  top: -4.75rem;
  left: 0;
  z-index: 2;
}

.p-work__btn-top {
  grid-row: 1/2;
  grid-column: 2/3;
}

.p-work__btn-left {
  grid-row: 2/3;
  grid-column: 1/2;
}

.p-work__btn-right {
  grid-row: 2/3;
  grid-column: 3/4;
}

.p-work__btn-under {
  grid-row: 3/4;
  grid-column: 2/3;
}